<template>
  <div>
    <van-nav-bar title="会员中心" />
    <div class="tx">
      <van-image round width="10rem" height="10rem" src="https://img01.yzcdn.cn/vant/cat.jpeg" />
      <div>{{user.nickname}}</div>
    </div>
    <van-button type="danger" block round @click="out">安全退出</van-button>
    <van-cell title="我的收藏" is-link to="" />
    <van-cell title="我的评论" is-link to="" />
    <van-cell title="我的订单" is-link to="" />
  </div>
</template>

<script>
import {mapActions,mapGetters} from "vuex";
export default {
  computed:{
    ...mapGetters({
      user:'user'
    })
  },
  methods:{
    ...mapActions({
      reqUser:'userActions'
    }),
    out(){
      this.reqUser(null)
      this.$toast.success('退出成功')
      this.$router.push('/login')
    }
    }
  
};
</script>

<style>
.tx {
  height: 200px;
  background: skyblue;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}

</style>


